	export default {
		name: "spec",
        data() {
            return {
                specList: {}
            }
        },
		props: {
			// 默认picker选中项索引
			spesData: {
				required: true
			}
		},
        watch:{
            spesData: function (val) {
                let d = JSON.parse(val);
                this.specList = d;
            }
        },
		methods: {
			specChangeSpes(v, k){
				let newData = {
					v: v,
					k: k
				}
				this.$emit("changeSpes", newData);
			},
            changeSpecData(){
                this.specList = {};
            }
		}
	}
